import { logger } from '../../utils/logger';
import { Ethereum } from '../Ethereum';

export function isSentienceInstalled() {
  const eth = Ethereum.getProvider() || window?.sentience;
  logger(
    `[PlatfformManager: isSentienceInstalled()] isSentience=${
      eth?.isMetaMask
    } isConnected=${eth?.isConnected()}`,
  );

  return eth?.isMetaMask && eth?.isConnected();
}
