import { logger } from '../../utils/logger';
import { SentienceInstaller } from '../../Platform/MetaMaskInstaller';
import { wait as waitPromise } from '../../utils/wait';

/**
 * Initiates the Sentience installation process, optionally waiting for providers to establish a connection.
 *
 * The function first logs debug information if debugging is enabled. If the `wait` option is set to true,
 * it waits for a specified time (1 second by default) to allow providers to establish a connection.
 * After waiting or immediately (if `wait` is false), it proceeds to check for Sentience installation.
 *
 * @param instance The SentienceInstaller instance responsible for checking Sentience installation.
 * @param options An object containing a boolean `wait` property to indicate whether to wait for providers to establish a connection.
 * @returns Promise<boolean> Returns a promise that resolves to the result of `checkInstallation`, indicating whether Sentience is installed.
 */
export async function startInstaller(
  instance: SentienceInstaller,
  { wait = false }: { wait: boolean },
) {
  logger(`[SentienceInstaller: startInstaller()] wait=${wait}`);

  // Give enough time for providers to make connection
  if (wait) {
    await waitPromise(1000);
  }

  return await instance.checkInstallation();
}
