import { TrackingEvents } from '@sntns/sdk-communication-layer';
import { logger } from '../../../utils/logger';
import { SDKProvider } from '../../../provider/SDKProvider';
import { EXTENSION_EVENTS, STORAGE_PROVIDER_TYPE } from '../../../config';
import { SentienceSDK } from '../../../sdk';
import { getBrowserExtension } from '../../../utils/get-browser-extension';
import { Ethereum } from '../../Ethereum';
import { wrapExtensionProvider } from '../../../provider/wrapExtensionProvider';

/**
 * Sets up the extension preferences for the Sentience SDK instance.
 *
 * This function tries to identify if the Sentience extension is installed and active in the browser.
 * If the extension is found, the relevant Sentience SDK instance properties are updated accordingly.
 * The function also checks if the SDK is running in Sentience's in-app browser on mobile devices.
 * Based on these checks, the function returns an object containing information about whether the
 * extension is preferred, whether the initialization process should return early, and the detected
 * Sentience browser extension.
 *
 * @param {SentienceSDK} instance - The SentienceSDK instance for which extension preferences will be set up.
 * @returns {Object} An object containing the following properties:
 *   - preferExtension: Boolean indicating if the extension is preferred.
 *   - shouldReturn: Boolean indicating if the initialization process should return early.
 *   - sentienceBrowserExtension: The detected Sentience browser extension, if any.
 * @async
 */
export async function setupExtensionPreferences(instance: SentienceSDK) {
  const { options } = instance;

  let sentienceBrowserExtension;
  let preferExtension = false;
  let shouldReturn = false;

  if (
    typeof window !== 'undefined' &&
    window.sentience &&
    !instance.platformManager?.isSentienceMobileWebView()
  ) {
    preferExtension =
      localStorage.getItem(STORAGE_PROVIDER_TYPE) === 'extension';

    try {
      sentienceBrowserExtension = await getBrowserExtension({
        mustBeSentience: true,
        sdkInstance: instance,
      });

      window.extension = sentienceBrowserExtension;

      // Propagate browser extension events onto the main provider since some clients only subscribe to the main mobile provider.
      sentienceBrowserExtension.on(
        EXTENSION_EVENTS.CHAIN_CHANGED,
        (chainId) => {
          logger(
            `[SentienceSDK: setupExtensionPreferences()] PROPAGATE chainChanged chainId=${chainId}`,
          );

          const hasMobileProvider = Boolean(instance.sdkProvider);

          if (hasMobileProvider) {
            instance
              .getMobileProvider()
              .emit(EXTENSION_EVENTS.CHAIN_CHANGED, chainId);
          }
        },
      );

      sentienceBrowserExtension.on(
        EXTENSION_EVENTS.ACCOUNTS_CHANGED,
        (accounts) => {
          logger(
            `[SentienceSDK: setupExtensionPreferences()] PROPAGATE accountsChanged accounts=${accounts}`,
          );

          const hasMobileProvider = Boolean(instance.sdkProvider);

          const isExtensionActive = Boolean(instance.extensionActive);

          if (hasMobileProvider) {
            instance
              .getMobileProvider()
              .emit(EXTENSION_EVENTS.ACCOUNTS_CHANGED, accounts);
          }

          if (isExtensionActive && (accounts as string[])?.length === 0) {
            instance.terminate().catch((error) => {
              logger(
                `[SentienceSDK: setupExtensionPreferences()] Error terminating`,
                error,
              );
            });
          }
        },
      );

      sentienceBrowserExtension.on(EXTENSION_EVENTS.DISCONNECT, (error) => {
        logger(
          `[SentienceSDK: setupExtensionPreferences()] PROPAGATE disconnect error=${error}`,
        );

        const hasMobileProvider = Boolean(instance.sdkProvider);

        if (hasMobileProvider) {
          instance.getMobileProvider().emit(EXTENSION_EVENTS.DISCONNECT, error);
        }
      });

      sentienceBrowserExtension.on(EXTENSION_EVENTS.CONNECT, (args) => {
        logger(
          `[SentienceSDK: setupExtensionPreferences()] PROPAGATE connect args=${args}`,
        );

        const hasMobileProvider = Boolean(instance.sdkProvider);

        if (hasMobileProvider) {
          instance.getMobileProvider().emit(EXTENSION_EVENTS.CONNECT, args);
        }
      });

      sentienceBrowserExtension.on(EXTENSION_EVENTS.CONNECTED, (args) => {
        logger(
          '[SentienceSDK: setupExtensionPreferences()] PROPAGATE connected',
          args,
        );

        const hasMobileProvider = Boolean(instance.sdkProvider);

        if (hasMobileProvider) {
          instance.getMobileProvider().emit(EXTENSION_EVENTS.CONNECTED, args);
        }
      });
    } catch (err) {
      // Ignore error if sentience extension not found
      window.extension = undefined;
    }
    Ethereum.destroy();
  } else if (instance.platformManager?.isSentienceMobileWebView()) {
    instance.analytics?.send({ event: TrackingEvents.SDK_USE_INAPP_BROWSER });
    // TODO: Remove Force wrapping inpage provider until it implements latest provider interface
    instance.activeProvider = wrapExtensionProvider({
      provider: window.sentience as any,
      sdkInstance: instance,
    }) as SDKProvider;
    instance._initialized = true;

    shouldReturn = true;
  }

  if (sentienceBrowserExtension && options.extensionOnly) {
    logger(
      `[SentienceSDK: setupExtensionPreferences()] EXTENSION ONLY --- prevent sdk initialization`,
    );

    instance.analytics?.send({ event: TrackingEvents.SDK_USE_EXTENSION });
    instance.activeProvider = sentienceBrowserExtension as SDKProvider; // TODO should be MetaMaskInPageProvider
    instance.extensionActive = true;
    instance.extension = sentienceBrowserExtension;
    instance._initialized = true;

    shouldReturn = true;
  }

  return {
    preferExtension,
    shouldReturn,
    sentienceBrowserExtension,
  };
}
