import { logger } from '../../utils/logger';
import { SentienceInstaller } from '../../Platform/MetaMaskInstaller';

/**
 * Checks for the installation of the Sentience extension and redirects to the installation page if not installed.
 *
 * The function first verifies if Sentience is already installed using the platformManager of the given SentienceInstaller state.
 * If Sentience is installed, the function returns true. If not, it triggers a redirection to the appropriate installation page.
 *
 * @param instance The SentienceInstaller instance used to check installation and perform redirection.
 * @returns Promise<boolean> Returns a promise that resolves to true if Sentience is installed, otherwise redirects to the installation page.
 */
export async function checkInstallation(instance: SentienceInstaller) {
  const { state } = instance;

  const isInstalled = state.platformManager?.isSentienceInstalled();

  logger(
    `[SentienceInstaller: checkInstallation()] isInstalled=${isInstalled}`,
  );

  // No need to do anything
  if (isInstalled) {
    return true;
  }

  return await instance.redirectToProperInstall();
}
