import { TrackingEvents } from '@sntns/sdk-communication-layer';
import { STORAGE_PROVIDER_TYPE } from '../../../config';
import { SentienceSDK } from '../../../sdk';
import { logger } from '../../../utils/logger';
import { connectWithExtensionProvider } from '../ProviderManager';

/**
 * Handles automatic and extension-based connections for Sentience SDK.
 *
 * This function decides between connecting using Sentience extension or automatically
 * connecting based on the passed parameters and platform conditions. If 'preferExtension' is true,
 * it attempts to connect with the Sentience extension and falls back to cleaning the preference
 * if the connection fails. If 'checkInstallationImmediately' is set in options, it attempts to auto-connect,
 * but only if the platform is a desktop web environment.
 *
 * @param instance The SentienceSDK instance to handle the connection for.
 * @param preferExtension A boolean flag indicating whether to prefer connecting via Sentience extension.
 * @returns void
 */
export async function handleAutoAndExtensionConnections(
  instance: SentienceSDK,
  preferExtension: boolean,
) {
  const { options } = instance;

  if (preferExtension) {
    logger(
      `[SentienceSDK: handleAutoAndExtensionConnections()] preferExtension is detected -- connect with it.`,
    );

    instance.analytics?.send({
      event: TrackingEvents.SDK_EXTENSION_UTILIZED,
    });

    connectWithExtensionProvider(instance).catch((_err) => {
      console.warn(`Can't connect with Sentience extension...`, _err);
      // Clean preferences
      localStorage.removeItem(STORAGE_PROVIDER_TYPE);
    });
  } else if (options.checkInstallationImmediately) {
    if (instance.platformManager?.isDesktopWeb()) {
      logger(
        `[SentienceSDK: handleAutoAndExtensionConnections()] checkInstallationImmediately`,
      );

      // Don't block /await initialization on autoconnect
      instance.connect().catch((_err) => {
        // ignore error on autoconnect
        logger(
          `[SentienceSDK: handleAutoAndExtensionConnections()] checkInstallationImmediately --- IGNORED --- error on autoconnect _err=${_err}`,
        );
      });
    } else {
      console.warn(
        `[handleAutoAndExtensionConnections()] checkInstallationImmediately --- IGNORED --- only for web desktop`,
      );
    }
  }

  instance._initialized = true;
}
