import { logger } from '../../../utils/logger';
import { RPC_METHODS } from '../../../config';
import { SentienceSDK } from '../../../sdk';

export async function connectWith({
  instance,
  rpc,
}: {
  instance: SentienceSDK;
  rpc: { method: string; params: any[] };
}) {
  if (!instance._initialized) {
    logger(
      `[SentienceSDK: connectWith()] provider not ready -- wait for init()`,
    );

    await instance.init();
  }

  logger(`[SentienceSDK: connectWith()] method: ${rpc.method} rpc=${rpc}`);

  if (!instance.activeProvider) {
    throw new Error(`SDK state invalid -- undefined provider`);
  }

  return instance.activeProvider.request({
    method: RPC_METHODS.SENTIENCE_CONNECTWITH,
    params: [rpc],
  });
}
